@use "../configs/variables.scss";

.item {
  border-radius: 9px;
  background-color: var(--color-light-s);
  transition: box-shadow 200ms ease 0s, background-color 300ms ease 0s;

  &:hover,
  &:focus-within {
    background-color: var(--color-light-xl);
    box-shadow: 0 2px 35px 14px rgb(13 13 13 / 4%);

    .button {
      border-color: var(--color-primary-light);
      background-color: var(--color-primary-light);
    }
  }
}

.item__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 270px;
  min-height: 435px;
  padding-bottom: 30px;
}

.item__name {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 400;
  font-size: var(--fz-slider-title);
  line-height: var(--lh-slider-title);
  text-align: center;
}

.item__image {
  order: -1;
  width: 270px;
  height: 270px;
  margin-bottom: 30px;
  background-color: var(--color-light-s);
}
