@use "../configs/variables.scss";

.hero {
  min-height: 908px;
  padding-top: 180px;
  padding-bottom: 0;
  background:
    url("../img/bg-pattern.png"),
    radial-gradient(100% 215.42% at 0% 0%, #5b483a 0%, #262425 100%);

  @media (max-width: variables.$laptop-width) {
    padding-top: 120px;
  }

  @media (max-width: variables.$tablet-width) {
    min-height: auto;
  }
}

.hero__container {
  display: flex;
  justify-content: space-between;

  @media (max-width: variables.$laptop-width) {
    flex-direction: column;
    align-items: center;
  }
}

.hero__column {
  max-width: 460px;
  margin-top: 163px;

  @media (max-width: variables.$laptop-width) {
    margin-top: 30px;
    margin-bottom: 101px;
  }

  @media (max-width: variables.$tablet-width) {
    margin-bottom: 105px;
    text-align: center;
  }
}

.hero__strong-text {
  display: block;
  margin-bottom: 42px;
  color: var(--color-light-xl);
  font-weight: 400;
  font-family: "Georgia", serif;

  @media (max-width: variables.$tablet-width) {
    letter-spacing: 0.06em;
  }
}

.hero__text {
  margin-bottom: 42px;
  color: var(--color-dark-s);
}

.hero__button {
  display: block;
  width: fit-content;

  @media (max-width: variables.$laptop-width) {
    margin: 0 auto;
  }
}

.hero__image {
  @media (max-width: variables.$laptop-width) {
    align-self: end;
    width: 569px;
    height: 593px;
  }

  @media (max-width: variables.$tablet-width) {
    align-self: end;
    width: 260px;
    height: 272px;
  }
}
