@font-face {
  font-weight: 400;
  font-family: "Arial";
  font-style: normal;
  font-display: swap;
  src: local("Arial"), url("../fonts/arial-regular.woff2") format("woff2"), url("../fonts/arial-regular.ttf") format("ttf");
}
@font-face {
  font-weight: 400;
  font-family: "Georgia";
  font-style: normal;
  font-display: swap;
  src: local("Georgia"), url("../fonts/georgia-regular.woff2") format("woff2"), url("../fonts/georgia-regular.ttf") format("ttf");
}
@font-face {
  font-weight: 700;
  font-family: "Georgia";
  font-style: normal;
  font-display: swap;
  src: local("Georgia bold"), url("../fonts/georgia-bold.woff2") format("woff2"), url("../fonts/georgia-bold.ttf") format("ttf");
}
:root {
  --color-dark-3xl: #292929;
  --color-dark-3xl-a60: rgb(41 41 41 / 60%);
  --color-dark-2xl: #444444;
  --color-dark-xl: #4c4c4c;
  --color-dark-l: #545454;
  --color-dark-m: #b2b2b2;
  --color-dark-s: #cdcdcd;
  --color-light-s: #fafafa;
  --color-light-l: #f6f6f6;
  --color-light-xl: #ffffff;
  --color-primary: #f1cdb3;
  --color-primary-light: #fddcc4;
  --fz-nav-link: 15px;
  --lh-nav-link: 24px;
  --fz-strong-text: 44px;
  --lh-strong-text: 57px;
  --fz-section-text: 15px;
  --lh-section-text: 24px;
  --fz-section-title: 35px;
  --lh-section-title: 45px;
  --fz-slider-title: 20px;
  --lh-slider-title: 23px;
  --fz-button: 17px;
  --lh-button: 22px;
  --fz-main-text: 20px;
  --lh-main-text: 23px;
}

@media (max-width: 767px) {
  :root {
    --fz-nav-link: 32px;
    --lh-nav-link: 51px;
    --fz-strong-text: 25px;
    --lh-strong-text: 32px;
    --fz-main-text: 15px;
    --lh-main-text: 16px;
    --fz-section-title: 25px;
    --lh-section-title: 32px;
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

.page {
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

.page__body {
  display: grid;
  grid-template-rows: 1fr min-content;
  min-height: 100%;
  margin: 0;
  color: var(--color-dark-xl);
  font-weight: 400;
  font-family: "Arial", sans-serif;
  font-size: var(--fz-section-text);
  line-height: var(--lh-section-text);
  background-color: #ffffff;
}

img {
  max-width: 100%;
  /* чтобы картинка не стала больше чем её контейнер */
  height: auto;
  /* картинка сохраняет пропорции */
  vertical-align: top;
}

button {
  padding: 0;
  border: none;
  /* в норме есть обводка */
  font: inherit;
  /* в норме monospace */
  font-size: 20px;
  background-color: transparent;
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1279px) {
  .container {
    max-width: 768px;
    padding: 0 30px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 10px;
  }
}

.button {
  display: inline-block;
  padding: 13px 43px;
  border: 2px solid var(--color-primary);
  border-radius: 100px;
  color: var(--color-dark-3xl);
  font-family: "Georgia", serif;
  font-size: var(--fz-button);
  line-height: var(--lh-button);
  letter-spacing: 0.06em;
  text-align: center;
  background-color: var(--color-primary);
  cursor: pointer;
  transition: background-color 200ms ease 0s, border-color 200ms ease 0s;
}
.button:hover, .button:focus {
  border-color: var(--color-primary-light);
  background-color: var(--color-primary-light);
}
.button--border {
  background-color: transparent;
}

.section {
  padding-top: 80px;
  padding-bottom: 100px;
}
.section--dark {
  background-color: var(--color-light-l);
}
@media (max-width: 767px) {
  .section {
    padding-top: 42px;
    padding-bottom: 42px;
  }
}

.section__title {
  margin: 0;
  color: var(--color-dark-l);
  font-weight: 400;
  font-family: "Georgia", serif;
  font-size: var(--fz-section-title);
  line-height: var(--lh-section-title);
  letter-spacing: 0.06em;
}
.section__title--center {
  text-align: center;
}
.section__title--light {
  color: var(--color-light-xl);
}
@media (max-width: 767px) {
  .section__title {
    text-align: center;
  }
}

.section__text {
  margin: 0;
  font-family: "Arial", "Helvetica Neue", "Helvetica", sans-serif;
  font-size: var(--fz-section-text);
  line-height: var(--lh-section-text);
}

.section__text + .section__text {
  margin-top: 25px;
}
@media (max-width: 767px) {
  .section__text + .section__text {
    margin-top: 22px;
  }
}

.section__strong-text {
  font-size: var(--fz-strong-text);
  line-height: var(--lh-strong-text);
}

.section__main-text {
  margin: 0;
  font-weight: 400;
  font-family: "Georgia", serif;
  font-size: var(--fz-main-text);
  line-height: var(--lh-main-text);
  letter-spacing: 0.06em;
}

.header {
  position: absolute;
  width: 100%;
  padding-top: 60px;
  padding-bottom: 60px;
}
.header--pets {
  position: fixed;
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: var(--color-light-xl);
}
@media (max-width: 1279px) {
  .header {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}

.donation__container {
  display: flex;
  justify-content: center;
  gap: 30px;
}
@media (max-width: 1279px) {
  .donation__container {
    flex-direction: column;
    align-items: center;
    gap: 60px;
  }
}
@media (max-width: 767px) {
  .donation__container {
    gap: 42px;
  }
}

.donation__image {
  width: 505px;
  margin-top: 11px;
}
@media (max-width: 1279px) {
  .donation__image {
    order: 1;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .donation__image {
    width: 260px;
  }
}

.donation__column {
  max-width: 380px;
}

.donation__title {
  margin-bottom: 20px;
}

.donation__bank-text {
  margin: 0;
  margin-bottom: 20px;
  color: var(--color-dark-l);
  font-family: "Georgia", serif;
  font-size: 15px;
  line-height: 17px;
  letter-spacing: 0.06em;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 767px) {
  .donation__bank-text {
    letter-spacing: 0;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .donation__bank-text {
    line-height: 24px;
  }
}

.donation__card-number {
  position: relative;
  display: flex;
  align-items: center;
  width: 349px;
  min-height: 43px;
  margin-bottom: 20px;
  padding: 10px 15px;
  padding-left: 59px;
  border-radius: 9px;
  font-family: "Georgia", serif;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 0.06em;
  background-color: var(--color-primary);
  transition: background-color 200ms ease 0s;
}
.donation__card-number:hover, .donation__card-number:focus {
  background-color: var(--color-primary-light);
}
.donation__card-number::before {
  content: "";
  position: absolute;
  left: 15px;
  width: 30px;
  height: 23px;
  background-image: url("../img/sections/donation/credit-card.svg");
}
@media (max-width: 767px) {
  .donation__card-number {
    width: 281px;
    margin-right: auto;
    margin-left: auto;
    font-size: 15px;
    line-height: 17px;
  }
}

.donation__legal-information {
  margin: 0;
  color: var(--color-dark-m);
  font-style: italic;
  font-size: 12px;
  line-height: 18px;
}
@media (max-width: 767px) {
  .donation__legal-information {
    padding: 0 10px;
    text-align: justify;
  }
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (width < 767px) {
  .navigation--open {
    justify-content: end;
  }
}

.navigation__logo {
  position: relative;
  z-index: 10;
  flex-shrink: 0;
}

@media (width <= 767px) {
  .navigation--open .navigation__logo {
    margin-right: 43px;
  }
}

/* navigation__list */
.navigation__list {
  display: flex;
  flex-wrap: wrap;
  gap: 36px;
  list-style: none;
}
@media (width <= 767px) {
  .navigation__list {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 5;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    visibility: hidden;
    transform: translateX(100%);
    transition: transform 0.3s ease, visibility 0ms 200ms;
  }
}

@media (max-width: 767px) {
  .navigation--open .navigation__list {
    width: 320px;
    height: 100vh;
    margin: 0;
    padding: 0;
    background-color: var(--color-dark-3xl);
    visibility: visible;
    transform: translateX(0%);
    transition: transform 0.3s ease, visibility 0ms 0ms;
  }
  .navigation--open .navigation__list::before {
    position: absolute;
    right: 320px;
    left: -200vw;
    height: 106vh;
    background-color: var(--color-dark-3xl-a60);
    content: "";
  }
}

@media (max-width: 767px) {
  .header--pets .navigation__list {
    background-color: var(--color-light-xl);
  }
}

.navigation__link {
  display: block;
  color: var(--color-dark-s);
  font-size: var(--fz-nav-link);
  line-height: var(--lh-nav-link);
  transition: color 200ms ease 0s, border 200ms ease 0s;
}
.navigation__link:hover, .navigation__link:focus {
  color: var(--color-light-s);
}
.navigation__link--active {
  border-bottom: 3px solid var(--color-primary);
}

.header--pets .navigation__link {
  color: var(--color-dark-l);
}
.header--pets .navigation__link:hover, .header--pets .navigation__link:focus {
  color: var(--color-dark-3xl);
}

/* toggle */
.navigation__toggle {
  position: relative;
  z-index: 6;
  display: none;
  flex-shrink: 0;
  width: 30px;
  height: 30px;
  margin-right: 43px;
}
@media (width <= 767px) {
  .navigation__toggle {
    display: block;
  }
}

.navigation__toggle-line {
  display: block;
  height: 2px;
  background-color: var(--color-primary);
  transition: transform 200ms ease 0s;
}
.navigation__toggle-line::before, .navigation__toggle-line::after {
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: var(--color-primary);
  content: "";
  transition: top 200ms ease 150ms, bottom 200ms ease 150ms;
}
.navigation__toggle-line::before {
  top: 4px;
  left: 0;
}
.navigation__toggle-line::after {
  bottom: 4px;
  left: 0;
}

.navigation--open .navigation__toggle-line {
  transform: rotate(90deg);
}
.navigation--open .navigation__toggle-line::before {
  top: 10px;
}
.navigation--open .navigation__toggle-line::after {
  bottom: 10px;
}

.hero {
  min-height: 908px;
  padding-top: 180px;
  padding-bottom: 0;
  background: url("../img/bg-pattern.png"), radial-gradient(100% 215.42% at 0% 0%, #5b483a 0%, #262425 100%);
}
@media (max-width: 1279px) {
  .hero {
    padding-top: 120px;
  }
}
@media (max-width: 767px) {
  .hero {
    min-height: auto;
  }
}

.hero__container {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1279px) {
  .hero__container {
    flex-direction: column;
    align-items: center;
  }
}

.hero__column {
  max-width: 460px;
  margin-top: 163px;
}
@media (max-width: 1279px) {
  .hero__column {
    margin-top: 30px;
    margin-bottom: 101px;
  }
}
@media (max-width: 767px) {
  .hero__column {
    margin-bottom: 105px;
    text-align: center;
  }
}

.hero__strong-text {
  display: block;
  margin-bottom: 42px;
  color: var(--color-light-xl);
  font-weight: 400;
  font-family: "Georgia", serif;
}
@media (max-width: 767px) {
  .hero__strong-text {
    letter-spacing: 0.06em;
  }
}

.hero__text {
  margin-bottom: 42px;
  color: var(--color-dark-s);
}

.hero__button {
  display: block;
  width: fit-content;
}
@media (max-width: 1279px) {
  .hero__button {
    margin: 0 auto;
  }
}

@media (max-width: 1279px) {
  .hero__image {
    align-self: end;
    width: 569px;
    height: 593px;
  }
}
@media (max-width: 767px) {
  .hero__image {
    align-self: end;
    width: 260px;
    height: 272px;
  }
}

.about__row {
  display: flex;
}
@media (max-width: 1279px) {
  .about__row {
    flex-direction: column;
    align-items: center;
    gap: 80px;
  }
}
@media (max-width: 767px) {
  .about__row {
    gap: 42px;
    padding: 0 15px;
    text-align: justify;
  }
}

.about__title {
  margin-bottom: 25px;
}

.about__column {
  flex-basis: 50%;
}
@media (max-width: 1279px) {
  .about__column--image {
    order: 1;
  }
}
@media (min-width: 1280px) {
  .about__column--text {
    margin-left: -5px;
  }
}

.about__image-box {
  position: relative;
  width: 300px;
  height: 408px;
  margin-left: 175px;
}
.about__image-box img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 1279px) {
  .about__image-box {
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .about__image-box {
    width: 260px;
    height: 353px;
  }
}

.about__text-column {
  max-width: 430px;
}
@media (min-width: 1280px) {
  .about__text-column {
    padding-top: 14px;
  }
}

.friends__title {
  max-width: 410px;
  margin: 0 auto;
  margin-bottom: 60px;
}
@media (max-width: 767px) {
  .friends__title {
    margin-bottom: 42px;
  }
}

.friends__slider {
  width: 100%;
  margin-bottom: 60px;
}
@media (max-width: 767px) {
  .friends__slider {
    margin-bottom: 42px;
  }
}

.friends__button {
  display: block;
  width: fit-content;
  margin: 0 auto;
}

.slider {
  position: relative;
}

.slider__wrapper {
  width: 990px;
  margin: 0 auto;
  overflow: hidden;
}
@media (max-width: 1279px) {
  .slider__wrapper {
    width: 580px;
  }
}
@media (max-width: 767px) {
  .slider__wrapper {
    width: 270px;
  }
}

.slider__carousel {
  position: relative;
  left: calc(-100% - 40px);
  display: flex;
  gap: 40px;
}

@keyframes move-right {
  from {
    left: calc(-100% - 40px);
  }
  to {
    left: calc(-200% - 80px);
  }
}
@keyframes move-left {
  from {
    left: calc(-100% - 40px);
  }
  to {
    left: 0%;
  }
}
.transition-right {
  animation-name: move-right;
  animation-duration: 700ms;
}

.transition-left {
  animation-name: move-left;
  animation-duration: 700ms;
}

